body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Droid Serif', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6{
  margin: 0;
  padding: 0;
  text-transform: capitalize;
  text-decoration: none;
}

p, li, ul {
  margin: 0;
  padding: 0;
  text-transform: none;
  list-style-type: none;
}
.flex {
  display: flex;
}

.column {
  flex-direction: column;
}
.header {
    background-color: #4e4747;
    color: white;
    position: fixed;
    align-items: center;
    justify-content: space-between;
    top: 0;
    height: 130px;
    width: 100%;
    text-transform: capitalize;
    z-index: 7;
}

.nav {
    display: flex;
}

.nav-mobile {
    display: none;
}

.a-link {
    margin: 10px;
    text-decoration: none;
    text-transform: capitalize;
    font-size: 18px;
}

.a-link>img {
    width: auto;
    height: 124px;
    border-radius: 50%;
}
.a-link:visited {
    color: inherit;
}
.a-link:hover {
    color: #d49863;
}

.a-link:active {
    color: #d49863;
}

.mobile-menu {
    float: right;
    order: 1;
    width: 100px;
    z-index: 8;
    cursor: pointer;
}

.line {
    position: absolute;
    z-index: 10;
    color: white;
    /* height: 15px; */
    width: 40px;
    /* left: 2%; */
    /* background: $main-color; */
    /* border-radius: 10px; */
    -webkit-transition: all cubic-bezier(0.25, 0.1, 0.28, 1.54) 0.32s;
    transition: all cubic-bezier(0.25, 0.1, 0.28, 1.54) 0.32s;
    /* height: 3px; */
    border-bottom: 3px solid white;
    cursor: pointer;
    
}
  
.line01 {
    top: 38%;
    right: 10px;
}

.line02 {
    top: 49%;
}

.line03 {
    top: 60%;
}

@media (max-width: 600px) {
    .nav {
        display: none;
    }
    .nav-mobile {
        display: flex;
    }
}
.home {
    /* margin-top: 120px; */
    /* height: 300px; */
    padding-top: 130px;
    /* padding-bottom: 100px; */
    /* position: absolute; */
}

.home-img {
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-position: center center;
    background-size: cover;
    width: 100%;
    height: 500px;
    z-index: 1;
    opacity: 0.5;
}
.home-container {
    height: 500px;
    width: 100%;
    padding-top: 20px;
    color: #9a5a22;
    z-index: 3;
    position: absolute;
    top: 118px;
    justify-content: space-between;
    margin-bottom: 40px;
    
}                    

.home-container>h1 {
    font-style: italic;
    margin-bottom: 35px;
    padding-top: 10px;
    text-shadow: 3px 1px 0px white;
    font-size: 3em;
    opacity: 1;
}

.home-title {
    font-size: 1.5em;
    padding-top: 15px;
    padding-left: 15px;
    margin: 20px auto;
    text-shadow: 3px 1px 0px #c7bebe;
}

.home-details {
    justify-content: space-between;
    align-items: center;
    margin: auto 20px;
}
.portfolio {
}

.portfolio-list {
    /* margin: auto; */
    flex-wrap: wrap;
    justify-content: center;
}

.portfolio>h1 {
    color: #9a5a22;
    text-shadow: 3px 1px 0px #c7bebe;
    font-style: italic;
    margin: 40px auto;
    padding-top: 140px;
}
.project-preview {
    border: 1px solid #e2d7d7;
    background-color: #efefef;
    box-shadow: 1px 1px 20px 2px #4e4d4d;
    width: 350px;
    height: 440px;
    justify-content: space-between;
    align-items: center;
    margin: 15px 25px;
    position: relative;
}

.a-img {
    width: 250px;
    height: 190px;
    background-size: cover; 
    background-position: center; 
    background-repeat: no-repeat;
    /* position: absolute; */
    top: 55px;
    left: 50px;
    margin-bottom: 20px;
}

.project-title {
    height: 50px;
}
.project-name {
    font-size: 20px;
    font-weight: bold;
    align-items: center;
    margin-right: 5px;
}

.project-title>p {
    align-self: center;
}

.links {
    /* position: absolute; */
    bottom: 15px;
    left: 50px;
    margin: 10px auto;
    /* margin-bottom: 20px; */
}

.links>a {
    border: 1px solid #dcc3c3;
    background-color: #4e4747d9;
    color: white;
    width: 70px;
    padding: 4px;
    margin: 0 10px;
    text-decoration: none;
    /* border-radius: 4px; */
}

.links>a:hover {
    background-color: black;
}

.ul-labels {
    /* position: absolute; */
    bottom: 50px;
    left: 0px;
    overflow-x: auto;
    font-size: 0.75em;
    padding: 0;
    flex-wrap: wrap;
    margin: auto;
}

.label {
    border: 1px solid #dcc3c3;
    list-style-type: none;
    padding: 4px;
    margin: 0 5px;
    border-radius: 4px;
    min-width: 50px;
}

.preview-bottom {
    /* position: absolute; */
    bottom: 10px;
    /* left: 0px; */
}

.desc {
    height: 70px;
    padding: 0 10px;
}

@media (max-width: 817px) {
    .project-preview {
        margin: 15px;
    }
}

@media (max-width: 400px) {
    .project-preview {
        width: 90%;
        height: 500px;
    }
    .desc {
        /* margin-bottom: 10px; */
        height: auto;
    }
    .project-name {
        font-size: 18px;
    }
    .label {
        margin: 10px;
    }
}
.about {
    width: 70%;
    margin: 100px auto;
    line-height: 1.75;
}

.about>h1 {
    color: #9a5a22;
    text-shadow: 3px 1px 0px #c7bebe;
    font-style: italic;
    padding-top: 140px;
}
.about-text-container {
    text-align: start;
}
.about-text {
    margin-bottom: 25px;
}
.education-list {
    margin-left: 50px;
}

.education-list>li {
    list-style-type: disc;
}

.labels {
    margin-bottom: 25px;
    flex-wrap: wrap;
}
.labels>p {
    border: 1px solid #dcc3c3;
    padding: 4px;
    margin: 10px 5px;
    border-radius: 4px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

@media (max-width: 600px) {
    .about {
        width: 85%;
    }
}
.contact-list {
    margin: auto;
    align-items: center;
    justify-content: center;
}

.hide {
    visibility: hidden;
}

.contact>h1 {
    color: #9a5a22;
    text-shadow: 3px 1px 0px #c7bebe;
    font-style: italic;
    margin: 40px auto;
    padding-top: 140px;
}

.link {
    font-size: 28px;
    color: #4e4747d9;
    margin: 10px;
}

.link:hover, .phone:hover {
    color: black;
}
.phone {
    cursor: pointer;
    color: #4e4747d9;
    font-size: 24px;
    margin: 10px;
}
.phone-num {
    margin-bottom: 50px;
}
.footer {
    height: 50px;
    width: 100%;
    background-color: #4e4747;
    opacity: 0.8;
    color: white;
    justify-content: center;
    align-items: center;
    /* position: fixed; */
    /* bottom: 0px; */
}
